.NewProduct {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: $DarkGray solid 5px;
  margin: 1rem;
  max-width: 100%;

  @include tablet {
    justify-content: flex-start;
    align-items: center;
    max-width: 30rem;
    margin: 1rem auto;
  }

  @include laptop {
    max-width: 40rem;
  }

  @include desktop {
    max-width: 50rem;
  }

  .image-upload {
    input[type="file"] {
      display: none;
    }
    img {
      width: 100%;
      height: 20rem;
      object-fit: cover;
      cursor: pointer;
    }
  }

  .TitleandOptions {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 1rem 0;
    width: 100%;

    .NameProduct {
      background-color: transparent;
      border: $negro solid 2px;
      font-family: Arial, Helvetica, sans-serif;
      font-size: 1rem;
      padding: 0.5rem;
      width: 45%;
      margin-left: 0.5rem;
      cursor: pointer;
      &::placeholder {
        color: $DarkGray;
        font-family: Arial, Helvetica, sans-serif;
        font-weight: bold;
      }
    }

    .type {
      background-color: transparent;
      border: $negro, solid 2px;
      border-radius: 2vh;
      padding: 0.5rem;
      margin-right: 0.5rem;
      width: 45%;
      font-family: Arial, Helvetica, sans-serif;
      cursor: pointer;
    }
  }

  .Description {
    width: 92%;
    padding: 0.5rem;
    background-color: transparent;
    border: $negro solid 2px;
    margin: 0.5rem;
    height: auto;
    min-height: 5rem;
    resize: vertical;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
    &::placeholder {
      color: $DarkGray;
      font-family: Arial, Helvetica, sans-serif;
      font-weight: bold;
    }
  }

  .Price {
    text-align: center;
    background-color: $CrimsonRed;
    border: solid 2px;
    border-color: darken($CrimsonRed, 20%);
    color: $blanco;
    font-family: Arial, Helvetica, sans-serif;
    width: 92%;
    padding: 0.5rem;
    margin: 0.5rem;
    cursor: pointer;
    &::placeholder {
      color: $blanco;
      font-family: Arial, Helvetica, sans-serif;
    }
  }

  .BTCreate {
    text-align: center;
    background-color: $ElectricBlue;
    border: solid 2px;
    border-color: darken($ElectricBlue, 20%);
    color: $blanco;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    width: 96.5%;
    padding: 0.5rem;
    margin: 0.5rem;
    transition: 1s;
    cursor: pointer;

    &:hover {
      background-color: darken($ElectricBlue, 20%);
      border-color: darken($ElectricBlue, 30%);
      border-radius: 2vh;
    }
  }
}
